import React  from "react";
import "./App.css";
import roma1 from "./Images/roma0.JPG";
import roma2 from "./Images/roma1.jpg";
import roma3 from "./Images/roma2.jpg";
import roma4 from "./Images/roma3.jpg";
import roma5 from "./Images/roma4.jpg";
import roma6 from "./Images/roma5.jpg";
import roma7 from "./Images/roma6.jpg";
import roma8 from "./Images/roma7.jpg";
import roma9 from "./Images/roma8.jpg";
import roma10 from "./Images/roma9.jpg";


let img=[roma1, roma2, roma3,roma4,roma5,roma6,roma7,roma8,roma9,roma10]
export function Modal(props) {
    
    return (
        <div id="main_modal">
            <div id="close"><span onClick={props.callback} >X</span></div>
            <div><span onClick={() => props.callback1(1)} id="right">&#62;</span></div>
            <div><span onClick={()=>props.callback2(1)} id="left">&#60;</span></div>
            <img src={img[props.props]} id="main_modal_img" alt="Italy" />
            
            
       </div>
   ) 

}