import React, { Component } from "react";
import './App.css';
import italy1 from "./Images/1.jpg"
import Slideshow from "./Slideshow"
import Information from "./Information"
import 'bootstrap/dist/css/bootstrap.css';
import {motion} from "framer-motion"

export default class Rome extends Component {
  
    Pagetransition = {
        in:{
      opacity:1,
      x:0
        },
        out:{
          opacity:0,
          x:"-100%"  
        }
      }
      Transition ={
        duration:.8,
        transition:"linear"
  }
  componentDidMount() {
    window.scrollTo(0, 0)
  }
  
    render() {
        return (
            <motion.div
        initial="out"
        animate="in"
        exit="out"
        variants={this.Pagetransition}
        transition={this.Transition}
        >
            <div className="main1">
            <div className="header1">
                <h1>ROME</h1>
            </div>
           
    
            <div className="body_image">
                    <img src={italy1} className="rome" alt="italy1"/>
            </div>
    
            <div className="content1">
               
                <article className="content02">
                    Our first destination was Rome. Ancient city, older more that 2000
                    thousand years. It is magical city, with perfect mixture between ancient and modern world.
                    Evry stone in Rome has it own history.<br></br>
                    We stay in Rome, four days. During this period of time we have tried to visite, the most of
                    Rome monuments. How ever this mission was impossible, espcialy because we have tried to do it
                    in such short time. Maybe that is the reason, why people says that this city is city of ethernity
                    and that is the reason, why all roads lead back to Rome.<br></br>
                    On the left side you will find usseful informations, about our accomodation, transport in and to Rome
                    and also, what we have visited. Below are photos, that you should not missout.<br></br>
                    I hope that this, short interactie story, will motivate you to visit this timeless city.
                </article>
    
                <Information/>
            </div>
            
        </div>
                <Slideshow />
                </motion.div>
        )
    }
}