import React from 'react';
import { Link,useNavigate} from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.css';
import Accordion from 'react-bootstrap/Accordion'
import Card from 'react-bootstrap/Card'

export default function Information() {
  const home = process.env.PUBLIC_URL + "/"
  const navigate = useNavigate()
  const handleClick = (e) => {
    e.preventDefault()
    navigate(home)
  }
  return (
    
      <nav className="content01">
      <Accordion>
  <Card >
    <Accordion.Toggle as={Card.Header} eventKey="0" id="accomodation">
      Accomodation
    </Accordion.Toggle>
    <Accordion.Collapse eventKey="0">
    <Card.Body className="modal1">
    <p>In Sicily we were accomodate in  Marku's House. </p>
    <p>
      Accomodation was very average, but count on that how close was to downtown, it worth for price.
    </p>
      </Card.Body>
    </Accordion.Collapse>
  </Card>
  <Card>
    <Accordion.Toggle as={Card.Header} eventKey="1" id="Transport">
      Transport
    </Accordion.Toggle>
    <Accordion.Collapse eventKey="1">
              <Card.Body className="modal2">
                <p>For traveling to beach we used local bus companies in Palermo and Catania.<br></br>
                    Transport was cheap and well organized. For far travelings you can use variety
                    of buss companies. You can rent a car. Price is 20 eur per day. Also you can
                    use train and enjoe in beautiful Sicily landscape.
                </p>
              </Card.Body>
    </Accordion.Collapse>
          </Card>
          <Card>
    <Accordion.Toggle as={Card.Header} eventKey="2" id="Places">
      Places
    </Accordion.Toggle>
    <Accordion.Collapse eventKey="2">
              <Card.Body className="modal3">
              <p> I think that ther are endless places in Sicily, that you can wisit. All you need is avanturistic
                    spirit,<br></br>
                    good energy and daily bas ticket.
                </p>
                <p>We have managed to visit:</p>
                <ul class="places">
                    <li>Teatro Masimo</li>
                    <li>Palermo catedral</li>
                    <li>Mondelo Beach</li>
                    <li>San Vito locopo</li>
                    <li>Ursino castle</li>
                    <li>Taormina</li>
                </ul>
              </Card.Body>
    </Accordion.Collapse>
  </Card>
</Accordion>
      <button id="button"><Link to="/" onClick={handleClick}>BACK TO ROME</Link></button>
  </nav>
      
          
          
          
  );
}