import React, { Component } from "react";
import './App.css';
import sicily from "./Images/sicily.jpg"
import Slideshow from "./Slideshow_sicily"
import Information from "./Information_sicily"
import 'bootstrap/dist/css/bootstrap.css';
import {motion} from "framer-motion"

export default class Rome extends Component {
    Pagetransition = {
        in:{
      opacity:1,
      x:0
        },
        out:{
          opacity:0,
          x:"-100%"  
        }
      }
      Transition ={
        duration:.8,
        transition:"linear"
  }
  componentDidMount() {
    window.scrollTo(0, 0)
  }
    render() {
        return (
            <motion.div
        initial="out"
        animate="in"
        exit="out"
        variants={this.Pagetransition}
        transition={this.Transition}
        >
            <div className="main1">
            <div className="header1">
                <h1>SICILY</h1>
            </div>
           
    
            <div className="body_image">
                    <img src={sicily} className="rome" alt="sicily"/>
            </div>
    
            <div className="content1">
               
                <article className="content02">
                Sicily, place where Medeterian, meet history, place where people injoe in good food and good wine. This is place where people are diligent to family and christian faith.
Iceland is situated in the south of Italy and is large almost as Macedonia is. Transport is very well organized and you can chose to travel among variety of transports.
On Sicily evry person can something interesting. You can enjoe in very beautiful beaches, among them Mondelo and San Vito Locopo. Also every town has it's specific food and wines. Landscape is amazing. At the midle of the iceland is situated mighty vulcano mountain of Ethna. This is still active vulcano.
We visited three towns. First we were in Palermo, the capital of Sicily. After five days, we went to Catania by train. For me Catania is more beautiful than Palermo. Also we have a chance to visit Taormina. This is small romatic town, full with history in beauty.
If you ask adventure, Sicily is definitly place where you can find it.
                </article>
                <Information/>
    
            </div>
            
        </div>
                <Slideshow />
                </motion.div>
        )
    }
}