import roma1 from "./Images/sicily0.jpg";
import roma2 from "./Images/sicily1.jpg";
import roma3 from "./Images/sicily2.jpg";
import roma4 from "./Images/sicily3.jpg";
import roma5 from "./Images/sicily4.jpg";
import roma6 from "./Images/sicily5.jpg";
import roma7 from "./Images/sicily6.jpg";
import roma8 from "./Images/sicily7.jpg";
import roma9 from "./Images/sicily8.jpg";
import roma10 from "./Images/sicily9.jpg";


import React, { Component } from "react";

export class Photos extends Component{

    render() {
        return (
            <div class="flex_conteiner">
        <h2>Photo Galery</h2>
                <img src={roma1} onClick={() => this.props.callback(0)} alt="Roma1" />
                <img src={roma2} onClick={ ()=>this.props.callback(1)} alt="Roma2" />
                <img src={roma3} onClick={ ()=>this.props.callback(2)} alt="Roma3"/>
                <img src={roma4} onClick={() => this.props.callback(3)} alt="Roma4" />
                <img src={roma5} onClick={() => this.props.callback(4)} alt="Roma5"  />
                <img src={roma6} onClick={() => this.props.callback(5)} alt="Roma6" />
                <img src={roma7} onClick={() => this.props.callback(6)} alt="Roma7"  />
                <img src={roma8} onClick={() => this.props.callback(7)} alt="Roma8"  />
                <img src={roma9} onClick={() => this.props.callback(8)} alt="Roma9" />
                <img src={roma10} onClick={ ()=>this.props.callback(9)} alt="Roma10" />
            </div>
        )
    }
}