import React, { Component } from 'react';
import "./App.css";

import { Photos } from './Photos';
import { Modal } from './Modal';





export default class Slideshow extends Component {

    constructor(props) {
        super(props);
        this.state = {
            counter: 0,
            visibility: false
        }
    }
    changeCounter = (val) => {
        this.setState({
            counter: this.state.counter + val,
            visibility:!this.state.visibility
        })
        
        
    }

    close = () => {
        this.setState({
            counter: 0,
            visibility:!this.state.visibility 
        })
    }
    forward = (val) => {
       
        if (this.state.counter < 9) {
            this.setState({
                counter: this.state.counter + val,
                
            })
                
        }
        else {
            this.setState({
                counter: 0
            })
        }
    }
    backward = (val) => {
       
        if (this.state.counter < 1) {
            this.setState({
                counter: 9
            }) 
                
        }
        else {
            
            this.setState({
                counter: this.state.counter - val,
                
            })
        }
    }
    render() {
       
        return (
            <div>
               
                
                <Photos callback={this.changeCounter} />
                {this.state.visibility && <Modal props={this.state.counter} callback={this.close}
                callback1={this.forward} callback2={this.backward}
                />}
            </div>    
            )
    }
}
