import React, { Component } from "react";
import Slider from "react-slick";
import './App.css';
import italy1 from "./Images/1.jpg"
import italy2 from "./Images/2.jpg"
import italy3 from "./Images/3.jpg"
import italy4 from "./Images/4.jpg"
import italy5 from "./Images/5.jpg"
import IndexBody from "./index_body"
import {motion} from "framer-motion"

export default class AutoPlay extends Component {
  componentDidMount() {
    window.scrollTo(0, 0)
  }
  Pagetransition = {
    in:{
  opacity:1,
  y: 0
    },
    out:{
      opacity:1,
      y:"-100%"  
    }
  }
  Transition ={
    duration:1.5,
    transition:"linear"
  }
  
  render() {
    const settings = {
      dots: true,
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,        
      fade: true,
      autoplay: true,
      speed: 3500,
      autoplaySpeed: 3500,
      cssEase: "linear",
      
    };
    return (
      <motion.div
        initial="out"
        animate="in"
        exit="out"
        variants={this.Pagetransition}
        transition={this.Transition}
        >
      <div className="main">
        <div class="header">
     
        <Slider {...settings}>
          <div>
            <img src={italy1} alt="italy1" className="italy_img"/>
          </div>
          <div>
          <img src={italy2} alt="italy2" className="italy_img"/>
          </div>
          <div>
          <img src={italy3} alt="italy3" className="italy_img"/>
          </div>
          <div>
          <img src={italy4} alt="italy4" className="italy_img"/>
          </div>
          <div>
          <img src={italy5} alt="italy5" className="italy_img"/>
          </div>
          
            </Slider>
            <div class="head1">
                <h1>ITALY IN OUR PALMS</h1>
            </div>
      </div>
        <IndexBody />
        </div>
        </motion.div>
    );
  }
}